import React, { useState } from "react";
import { useHistory } from "react-router-dom";
import parse from "html-react-parser";
import { Button } from "@mui/material";

import { useAuth } from "../../../../contexts/AuthContext";

const Plan = ({
  name,
  sub_title,
  icon,
  amount,
  recommended,
  isMobile,
  currency,
  plan_type,
  plan_detail,
  planId,
}) => {
  const [showAll, setShowAll] = useState(false);
  const planDetail = plan_detail.split("\n");
  const { token } = useAuth();
  const history = useHistory();
  const doContinue = () => {
    localStorage.setItem(
      "selectedPlanDetail",
      JSON.stringify([
        {
          planId: planId,
          amount: amount,
          redirect: "billing",
        },
      ])
    );
    const url = token ? `/business-setup` : `/sign-up`;
    history.push({
      pathname: url,
    });
  };
  const formatAmount = (amount) =>
    amount.toString().replace(/\B(?<!\.\d*)(?=(\d{3})+(?!\d))/g, ",");

  return (
    <div className={`plan-box ${recommended ? "recommended" : ""}`}>
      <h4 className="title">{name}</h4>
      <h5 className="subtitle">{sub_title}</h5>
      <div className="icon">{icon && parse(icon)}</div>
      <p className="price">
        {amount > 0 && (
          <sup className="symbol">{currency === "USD" ? "$" : "₹"}</sup>
        )}
        <span className="value">
          {amount > 0 ? formatAmount(amount) : "FREE"}
        </span>
        {amount > 0 && (
          <sub className="rate">{plan_type === "Monthly" ? "/mo" : ""}</sub>
        )}
      </p>
      <div className="action">
        <Button variant="contained" type="button" onClick={doContinue}>
          {"Get Started"}
        </Button>
      </div>
      <div className="ruler">
        <hr />
      </div>
      <ul
        className={`features${
          isMobile ? (showAll ? " show-all" : " show-less") : ""
        }`}
      >
        {planDetail.map((feature, index) => (
          <li key={index} className={feature.includes("*") ? "star" : ""}>
            {feature.includes("*") ? feature.slice(1) : feature}
          </li>
        ))}
      </ul>
      {isMobile ? (
        <div className={`expand${showAll ? " up" : " down"}`}>
          <button type="button" onClick={() => setShowAll(!showAll)}>
            {showAll ? "SHOW LESS FEATURES" : "SEE ALL FEATURES"}
          </button>
        </div>
      ) : (
        <div className="action">
          <Button variant="contained" type="button" onClick={doContinue}>
            {"Get Started"}
          </Button>
        </div>
      )}
    </div>
  );
};

export default Plan;
