import React from "react";

import FreeImg from "../assets/free.svg";
import ProImg from "../assets/pro.svg";

import Plan from "./plan";

const Scheduling = ({ className, isMobile, plans }) => {
  return (
    <section className={className}>
      <div className="content">
        <div className="plans-header">
          <span className="badge">{"CURATE"}</span>
          <h2 className="title">{"Social Media Scheduling"}</h2>
        </div>
        <div className="plans-lists">
          {plans.map((plan, index) => (
            <Plan key={index} isMobile={isMobile} {...plan} />
          ))}
        </div>
      </div>
    </section>
  );
};

export default Scheduling;
